@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.2/css/all.min.css);
@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
.stars-gray{position:relative;display:inline-block}.stars-yellow{position:absolute;top:0;left:0;white-space:nowrap;overflow:hidden;width:0}.stars-gray::before{content:"\f005 \f005 \f005 \f005 \f005";font-family:"Font Awesome 5 Free";font-weight:900;color:#ccc}.stars-yellow::before{content:"\f005 \f005 \f005 \f005 \f005";font-family:"Font Awesome 5 Free";font-weight:900;color:#f8ce0b}

.taggle-button{background-color:red}

.pagination{padding:0.5rem;margin-top:1rem;display:flex;justify-content:center;background-color:azure}

.navBar{padding:1rem}.navBar .link-nav{text-decoration:none;margin:1rem}

.header{width:100%}

.login-form{width:40rem;padding:1rem;background-color:#ffffff10;backdrop-filter:blur(12px);-webkit-backdrop-filter:blur(12px);border-radius:1rem;margin-right:2rem;color:white}.login-form .container-input{display:flex;flex-direction:column;margin-bottom:1rem}

.login{height:100vh;width:100vw;display:flex;justify-content:flex-start;align-items:center;background-image:url("https://images.pexels.com/photos/375885/pexels-photo-375885.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260");padding-left:2rem}

.singup-form{width:40rem;padding:1rem;background-color:#ffffff10;backdrop-filter:blur(12px);-webkit-backdrop-filter:blur(12px);border-radius:1rem;margin-right:2rem;color:white}.singup-form .container-input{display:flex;flex-direction:column;margin-bottom:1rem}

.singup{height:100vh;display:flex;justify-content:flex-start;align-items:center;background-image:url("https://images.pexels.com/photos/375885/pexels-photo-375885.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260");padding-left:2rem}

*{margin:0;padding:0;box-sizing:border-box}#root{display:flex;flex-direction:column;align-items:center;height:100%;font-family:'Quicksand', sans-serif;background-color:#3d405b}

